import cn from 'classnames';
import React from 'react';
import { ButtonStyled, ButtonWrap } from './Button.styled';

export const BUTTON_TYPES = {
  OUTLINE: 'outline',
  TRANSPARENT: 'transparent',
  GREEN: 'green',
  BLUE: 'blue',
  WHITE: 'white',
};

const BUTTON_STYLES = {
  outline: {
    color: '#9996AE',
    background: 'transparent',
    border: null,
    hover: 'opacity: 0.6;',
  },

  transparent: {
    color: '#CCD1F1',
    background: 'transparent',
    border: '1px solid #8b8a8e;',
    borderRadius: '7px',
    hover: 'opacity: 0.6;',
  },

  white: {
    color: '#17161D',
    background: '#FFFFFF',
    border: '1px solid #E5E5E5',
    hover: 'opacity: 0.6;',
  },
  green: {
    color: '#1F380B',
    background: '#BAE38B',
    border: null,
    hover: 'opacity: 0.6;',
  },

  blue: {
    color: '#FFFFFF',
    background: '#2D68F8',
    border: null,
    hover: 'opacity: 0.6;',
  },
};

export const Button = ({
  children,
  type = 'outline',
  width = '100%',
  height = '56px',
  margin = 0,
  disabled = false,
  font = 'T_30',
  className = '',
  isShowPlaceholder,
  isLoading,
  isSmall,
  isCta = true,
  onClick,
  buttonType = 'button',
  ...restProps
}) => {
  const onClickHandler = () => {
    onClick();
  };

  return (
    <ButtonWrap
      width={width}
      height={height}
      margin={margin}
      className={cn(className, 'buttonWrap', { small: isSmall })}
    >
      <ButtonStyled
        type={buttonType}
        disabled={disabled}
        className={cn('button', type, { disabled })}
        font={font}
        onClick={onClickHandler}
        {...BUTTON_STYLES[type]}
        {...restProps}
      >
        {children}
      </ButtonStyled>
    </ButtonWrap>
  );
};
